import * as React from 'react'

export class SignIn extends React.Component<any, any> {
    constructor(props: any) {
        super(props)
        this.state = {
            emailAdress: '',
            password: ''
        }
    }
    render() {
        return (
            <div className={'sign-in_container'}>
                <div className={'sign-in_buttons-navigation'}>
                    <button className={'sign-in_button-min active'}>
                        Log In
                    </button>
                    <button className={'sign-in_button-min'}>
                        Sign Up
                    </button>
                </div>
                <input  className={'sign-in_input'} placeholder={'Email adress'} value={this.state.emailAdress} onChange={(e) => this.setState({emailAdress: e.target.value})} />
                <input type='password' className={'sign-in_input'}  placeholder={'Password'} value={this.state.password} onChange={(e => this.setState({password: e.target.value}))} />
                <button className={'sign-in_button'} onClick={() => this.props.coonection(this.state.emailAdress, this.state.password)}>Started</button>
                { this.props.showErrorMessage && <div>Invalid username or password</div>}
            </div>
        )
    }
}