import * as React from 'react';

function outline(fun: any) {
    document.addEventListener('mousedown', fun)
}

export class PersonalArea extends React.Component<any, any> {
    private _flag: boolean;
    constructor(props) {
        super(props);
        this.state = {
            show: false,
        };
        this._flag = false
        outline(() => !this._flag && this.state.show && this.setState({show: false}))
    }
    
    render() {
        console.log(this.props.func)
        return (
            <div onClick={() => this.setState({show: !this.state.show})} onMouseOver={() => this._flag = true } onMouseOut={() => this._flag = false } className='personal-area'>
                <div className='personal-area_title'></div>
                { this.state.show && <div className='personal-area_contant'>
                    <button className='personal-area_li'>account </button>
                    <button onClick={() => this.props.func()} className='personal-area_li'>logOut</button>
                    
                </div> } 
            </div>
        )
    }
}