import * as React from 'react'

export class WorkSpase extends React.Component<any, any> {
    constructor(props: any) {
        super(props)
    }
    render() {
        return (
            <div className='work-spase'>
                <div className='work-spase_user'>
                    <div className='work-spase_button_full'>My reports</div>
                    <div className='work-spase_button_full'>Schedules</div>
                </div>

                { this.props.role === 'admin' &&
                    <div className='work-spase_developer'>
                        <div className='work-spase_button'>My Connections</div>
                        <div className='work-spase_button'>My Data stores</div>
                        <div className='work-spase_button'>My Data flows</div>
                        <div className='work-spase_button'>My Data sources</div>
                        <div className='work-spase_button'>Data Dictionary</div>
                        <div className='work-spase_button'>Data Loads monitor</div>
                    </div>
                }
            </div>
        )
    }
}