import * as React from 'react';
import { SignIn } from './sigin';
import { Connection } from '../connection';
import { WorkSpase } from './workSpase';
import { Loader } from './preloader';
import { Header } from './header';
import { SomeContent } from './someContent'

export class App extends React.Component<any, any> {
  private _connection: Connection;
  private _role: any;
  private _errorConnection: boolean;
  constructor(props: any) {
    super(props);
    this._role = localStorage.getItem('role')
    this.state = {
      connectionStatus: this._role ? 2 : 0
    }
    this._connection = new Connection;
    this._connection.subscribe((v: any) => {
      console.log(v.role);
      this._role = v.role;
      if (v.status === 0 && v.a !== true) {
        this._errorConnection = true;
      }
      localStorage.setItem('role', v.role)
      this.setState({connectionStatus: v.status});
    });
  }
  render() {
    return (
      <div>
        { this.state.connectionStatus === 0 && <SignIn coonection={this._connection.getInfo.bind(this._connection) } showErrorMessage={this._errorConnection}/> }
        { this.state.connectionStatus === 1 && <Loader/> }
        { this.state.connectionStatus === 2 && <div>
          <Header func={this._connection.disconnect.bind(this._connection)} />
          <SomeContent />
          <WorkSpase role={this._role} />
        </div> }
      </div>
    )
  }
}