export class Connection {
    private _subscribes: any;
    constructor() {
        this._subscribes = [];
    }
    public async getInfo(email: string, password: string): Promise<void> {
        const fiendUser = this._users().find((user) => user.email === email && user.password === password);
        this._fire(1, '');

        await new Promise((r) => setTimeout(r, 1000));

        if (fiendUser) {
            this._fire(2, fiendUser.role);
        } else {
            this._fire(0, '');
        }
    }

    public subscribe(fun: any) {
        this._subscribes.push(fun)
    }

    public disconnect() {
        this._fire(0, '', true);
    }

    private _fire(status: any, role: any, a: any) {
        this._subscribes.forEach((element: any) => {
            element({status: status, role: role, a: true})
        });
    }
    
    private _users(): any[] {
       return  [
            { 
            
                email: 'admin@gmail.com',
                password: '12345',
                role: 'admin'
            },
            { 
            
                email: '2',
                password: '2',
                role: 'admin'
            },
            {
                email: 'developer@gmail.com',
                password: '12345',
                role: 'developer'
            },
            {
                email: 'user@gmail.com',
                password: '12345',
                role: 'user'
            }
        ]
    }
}